import React, { useState, useEffect, Fragment, useCallback } from "react";
import { Typography, Grid, Paper, Box, Button } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../../../store/actions";
import { useTranslation } from "react-i18next";
import Input from "../../../components/Inputs/Input";
import PageError from "../../../components/common/PageError";
import TableSkeleton from "../../../components/common/TableSkeleton";
import IosSwitch from "../../../components/Inputs/IosSwitch";
import { isError } from "../../../util/generals";
import SaveRoundedIcon from "@material-ui/icons/SaveRounded";
import ButtonSpinner from "../../../components/UI/Spinners/ButtonSpinner";

const initialState = (shippings = []) => {
  let state = {
    status: {
      value: "",
      validity: false,
    },
    max_free: {
      value: "",
      validity: false,
    },
    flat_status: {
      value: "",
      validity: false,
    },
    flat_rate: {
      value: "",
      validity: false,
    },
    weight_status: {
      value: "",
      validity: false,
    },
    weight_base_amount: {
      value: "",
      validity: false,
    },
    weight_kg_amount: {
      value: "",
      validity: false,
    },
  };
  if (shippings.length) {
    for (const set of shippings) {
      if (set.key_id === "status") {
        state[set.key_id] = {
          value: { ...set, value: +set.value > 0 ? 0 : 1 },
          validity: !!set.value,
        };
      } else {
        state[set.key_id] = {
          value: set,
          validity: !!set.value,
        };
      }
    }
  }
  return state;
};

let timeout;
const Settings = () => {
  const settings = useSelector((state) => state.settings);
  const [state, setState] = useState({});
  const [mounted, setMounted] = useState(false);
  const [touched, setTouched] = useState(false);
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const handleStateResult = (value, validity, name) => {
    setState((prev) => {
      let statusDynamics = {};
      if (name === "flat_status" && value > 0) {
        statusDynamics = {
          status: {
            ...prev.status,
            value: { ...prev.status.value, value: 0 },
          },
          weight_status: {
            ...prev.weight_status,
            value: { ...prev.weight_status.value, value: 0 },
          },
        };
      } else if (name === "weight_status" && value > 0) {
        statusDynamics = {
          status: {
            ...prev.status,
            value: { ...prev.status.value, value: 0 },
          },
          flat_status: {
            ...prev.flat_status,
            value: { ...prev.flat_status.value, value: 0 },
          },
        };
      } else if (name === "status" && value > 0) {
        statusDynamics = {
          weight_status: {
            ...prev.weight_status,
            value: { ...prev.weight_status.value, value: 0 },
          },
          flat_status: {
            ...prev.flat_status,
            value: { ...prev.flat_status.value, value: 0 },
          },
        };
      }
      return {
        ...prev,
        ...statusDynamics,
        [name]: {
          value: { ...prev[name].value, value: value },
          validity: validity,
        },
      };
    });
  };

  const fetchShipping = useCallback(async () => {
    try {
      const shippings = await dispatch(actions.getShippings());
      setState(initialState(shippings));
    } finally {
      setMounted(true);
    }
  }, [dispatch]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchShipping();
    }, 0);
    return () => clearTimeout(timeout);
  }, [fetchShipping]);

  const handleSave = async () => {
    let body = {};
    let oneEnabled = false;
    for (const key in state) {
      body = { ...body, [key]: state[key].value };
      if (key.endsWith("status") && +state[key].value.value > 0) {
        oneEnabled = true;
      }
      if (!state[key].validity) {
        setTouched(true);
        return dispatch(
          actions.fireSnake("error", t("common:complete_red_input"))
        );
      }
    }

    if (!oneEnabled) {
      return dispatch(
        actions.fireSnake("warning", t("settings:enable_at_least_one_shipping"))
      );
    }

    try {
      const newShippings = await dispatch(actions.saveShippings(body));
      setState(initialState(newShippings));
      dispatch(actions.fireSnake("success", t("common:saved_success")));
    } catch (err) {
      //
    }
  };

  return (
    <Fragment>
      <Typography variant="h4" className="py" align="center">
        {t("settings:shipping_settings")}
      </Typography>

      {!mounted ? (
        <Paper>
          <TableSkeleton row={4} />
        </Paper>
      ) : !settings.loading && settings.errors ? (
        <PageError
          text={settings.errors.message || t("common:uncommon_err")}
          onTryAgain={fetchShipping}
        />
      ) : (
        <Fragment>
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <Paper className="p">
                <Box className="flex align-items-center flex-justify-between py">
                  <Typography className="py">
                    {t("settings:shippings_status")}
                  </Typography>
                  <IosSwitch
                    name="status"
                    state={state.status.value.value}
                    onChange={handleStateResult}
                  />
                </Box>
                {+state.status.value.value === 0 && (
                  <Input
                    label={t("settings:max_free")}
                    initialValue={state.max_free.value.value}
                    name="max_free"
                    getResult={handleStateResult}
                    decimal
                    mathMin="-1"
                    setTouched={touched}
                    serverError={isError(
                      settings.errors && settings.errors.data,
                      `max_free`
                    )}
                    helperNotice={t("settings:max_free_notice")}
                    withIcon={<Typography color="primary">$</Typography>}
                  />
                )}
              </Paper>
            </Grid>

            <Grid item md={6} sm={12} xs={12}>
              <Paper className="p">
                <Box className="flex align-items-center flex-justify-between py">
                  <Typography className="py">
                    {t("settings:flat_status")}
                  </Typography>
                  <IosSwitch
                    name="flat_status"
                    state={state.flat_status.value.value}
                    onChange={handleStateResult}
                  />
                </Box>
                {+state.flat_status.value.value > 0 && (
                  <Input
                    label={t("settings:flat_rate")}
                    initialValue={state.flat_rate.value.value}
                    name="flat_rate"
                    getResult={handleStateResult}
                    decimal
                    mathMin="0"
                    setTouched={touched}
                    serverError={isError(
                      settings.errors && settings.errors.data,
                      `flat_rate`
                    )}
                    withIcon={<Typography color="primary">$</Typography>}
                  />
                )}
              </Paper>
            </Grid>

            <Grid item md={6} sm={12} xs={12}>
              <Paper className="p">
                <Box className="flex align-items-center flex-justify-between py">
                  <Typography className="py">
                    {t("settings:weight_status")}
                  </Typography>
                  <IosSwitch
                    name="weight_status"
                    state={state.weight_status.value.value}
                    onChange={handleStateResult}
                  />
                </Box>
                {+state.weight_status.value.value > 0 && (
                  <Grid container spacing={1}>
                    <Grid item xs={6}>
                      <Input
                        label={t("settings:weight_base_amount")}
                        initialValue={state.weight_base_amount.value.value}
                        name="weight_base_amount"
                        getResult={handleStateResult}
                        decimal
                        mathMin="0"
                        setTouched={touched}
                        serverError={isError(
                          settings.errors && settings.errors.data,
                          `weight_base_amount`
                        )}
                        withIcon={<Typography color="primary">$</Typography>}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <Input
                        label={t("settings:weight_kg_amount")}
                        initialValue={state.weight_kg_amount.value.value}
                        name="weight_kg_amount"
                        getResult={handleStateResult}
                        decimal
                        mathMin="0"
                        setTouched={touched}
                        serverError={isError(
                          settings.errors && settings.errors.data,
                          `weight_kg_amount`
                        )}
                        withIcon={<Typography color="primary">$</Typography>}
                      />
                    </Grid>
                  </Grid>
                )}
              </Paper>
            </Grid>
          </Grid>

          {/* Submit */}
          <Box className="center py-1">
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={handleSave}
              endIcon={settings.itemLoading && <ButtonSpinner />}
              startIcon={<SaveRoundedIcon />}
              disabled={settings.itemLoading}
              style={{ width: "80%" }}
            >
              <Typography variant="h4" className="py-1">
                {t("settings:save_settings")}
              </Typography>
            </Button>
          </Box>
        </Fragment>
      )}
    </Fragment>
  );
};

export default Settings;
